html {
    font-family: Arial, Helvetica, sans-serif;
    line-height: 30px;
}

hr {
  border: 0;
  clear:both;
  display:block;
  width: 100%;
  background-color:black;
  height: 1px;
}

span {
    font-size: 22px;
}

.grid-cover {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(150, 150, 150, 0.5);
    border-radius: 1%;
    z-index: 100;
    opacity: 100%;
}

.game-over {
    font-size: 300%;
    font-weight: bolder;
}

.button {
    border-radius: 5%;
    display: flex;
    width: 30%;
    height: 10%;
    background: #68BD68;
    font-size: x-large;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    color: cornsilk;
}

.score {
    font-weight: 700;
    line-height: 24px;
    text-align: center;
    margin-right: 10px;
    border-radius: 5%;
    display: inline-block;
    vertical-align: middle;
}

.score-container {
    margin-left: auto;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.current {
    width: 80px;
    background: tomato;
}

.highest {
    width: 100px;
    background: lightskyblue;
}

.card {
    border-top: 4px solid #000066;;
    display: flex;
    margin: auto;
    box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.35);
    align-items: center;
    align-content: center;
    justify-content: center;
    width: 700px;
}

.header {
    align-items: center;
    display: flex;
    margin-bottom: 30px;
}

.title {
    margin: 0px;
    display: inline-block;
    font-size: 42px;
    position: relative;
    color: #000066;
}

.logo {
    display: inline-block;
    margin-right: 10px;
    width: 48px;
    height: 48px;
}

.explanation {
    font-size: 18px;
    margin-top: 8px;
}

.tile {
    width: 104px;
    height: 104px;
    border-radius: 5%;
    display: inline-block;
    margin-right: 12px;
}

.grid-row {
    margin-bottom: 6px;
    margin-top: 6px;
    margin-left: 15px;
    height: 104px;
}

.grid {
    background: white;
    position: relative;
    z-index: 2;
}

.grid-container {
    position: relative;
    display: flex;
    flex-direction: column;
    background: gray;
    width: 480px;
    height: 480px;
    border-radius: 1%;
    margin: auto;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-top: 12px;
    margin-bottom: 12px;
}
.game-container {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 100px;
    justify-content: center;
    align-content: center;
    width: 600px;
}

.empty {
    background: lightgray;
    position: relative;
    z-index: 1;
}

.tile-background {
    width: 104px;
    height: 104px;
    border-radius: 5%;
    background: lightgray;
    position: absolute;
    z-index: 0;
}